import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  Output,
  ViewChild
} from '@angular/core';
import { SearchBarComponent } from '@sofico-framework/ui-kit/components/search-bar';
import {
  OnSofFocus,
  SOF_FOCUS_COMPONENT
} from '@sofico-framework/ui-kit/directives/focus';

@Component({
  selector: 'sof-table-list-search-bar',
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <sof-search-bar
      #searchBar
      [placeholder]="tc + '.FILTER' | translate"
      (search)="changeTerm.emit($event)"
    ></sof-search-bar>
  `,
  providers: [
    {
      provide: SOF_FOCUS_COMPONENT,
      useExisting: TableListSearchBarComponent
    }
  ],
  styleUrls: ['./table-list-search-bar.component.scss']
})
export class TableListSearchBarComponent<T> implements OnSofFocus {
  @Input() tc: string;
  @Output() changeTerm = new EventEmitter<string>();

  @ViewChild('searchBar') searchBar: SearchBarComponent;
  constructor() {}

  sofFocus(): void {
    this.searchBar.sofFocus();
  }
}
